*{
  box-sizing: border-box;
}
.App {
  text-align: center;
}

h1 {
  position: absolute;
  left: 50%;
  width: 320px;
  transform: translateX(-50%);
  background-color: rgb(140, 158, 175);
  color: rgb(243, 239, 239);
  font-weight: 100;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  font-size: 18px;
  text-align: center;
  padding: 10px;
}

.container {
  width: 350px;
  height: 500px;
  background-color: rgb(59, 74, 77);
  position: absolute;
  left: 50%;
  top: 49%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 20px;
  color: bisque;
  padding: 50px;
}

input{
  margin-top: 20px;
}

input,
select {
    width: 30%;
    border: none;
    border-bottom: 1px solid #000000;
    padding: 8px;
    border-radius: 8px;
    margin-left: 3px;
}

input:focus,
select:focus {
    border: 1px solid #3d8ca0;
    outline: none;
}
 

button {
  margin: 60px 30px auto;
  width: 150px;
  height: 50px;
  background-color: #f5f5f5;
  color: rgb(8, 7, 7);
  font-size: 16px;
  border: none;
  border-radius: 5px;
}

button:hover{
  background-color: rgb(140, 158, 175);

}

.bmi{
  margin: 60px;
  width: 1px;
}